.countriesStateWrapper {
  display: flex;
  padding: 15px;
  max-width: 1000px;
  margin:auto;
  margin-top: 3%;
}

.countriesWrapper, .statesWrapper {
  width: 500px;
}

.countriesWrapper {
  cursor: pointer;
}

.statesWrapper {
  margin-top: 0px;
  cursor: pointer;
}

.country, .state {
  border: 1px solid black;
}

.dist-wrap {
  border: 1px solid black;
}

.c-19-wrapper {
  display: flex;
}

.left-section {
  flex-basis: 50%;
}

.right-section {
  flex-basis: 50%;
}
.Layout{
  margin-top: 1%;
}
.nav-links{
    margin-top: 1%;
    display: flex;
    max-width: 1000px;
    border: 1px solid black;
    color:black;
    padding: 0rem 0rem;
    margin:auto;
}
.nav-links1{
  display: flex;
  margin:auto;
  border: 1px solid black;
  border-top: 0ch;
  border-bottom: 0ch;
  border-left: 0ch;
  color:black;
  padding: 0.5rem 1rem;
  text-decoration: none;
}
.nav-links1:hover{
    background-color:aliceblue;
    border-radius: 0px;
    transition: all 0.2s ease-out;
}
ul li{
  list-style-type: none;
}
.App1{
    max-width: 1000px;
    border: 1px solid black;
    color:black;
    padding: 0.5rem 1rem;
    padding: 15px;
    margin:auto;
}